/** @jsx jsx */
import { css, jsx, keyframes } from "@emotion/core";
import {groupStyle, inputStyle, highLightStyle, barStyle, labelStyle} from '../styles/InputStyles';
jsx;

const Input = (props: any) => (
    <div className="group" css={groupStyle}>
        <input
            css={inputStyle}
            {...props}
        />
        <span className="highlight" css={highLightStyle} />
        <span className="bar" css={barStyle} />
        <label css={labelStyle} htmlFor="{props.id}">
            {props.label}
        </label>
        {!!props.error && <label>
            {props.error}
        </label>}
    </div>
)
export default Input;